.CreateTodoButton{
    background-color: aqua;
    box-shadow: 0px 5px 25px rgba(97, 218, 250,0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    font-weight: bold;
    color: white;
    display: flex;
    justify-content: center;
    height: 64px;
    width: 64px;
    transform: rotate(0);
    transition: .3s ease;
    margin-bottom: 100px;
}

.CreateTodoButton:hover{
    transform: rotate(224deg);
}