.TodoList{
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}

#LineUp {
    animation: 2s anim-lineUp ease-out infinite;
}

@keyframes anim-lineUp {
    0% {
    opacity: 0;
    transform: translateY(80%);
    }
    20% {
    opacity: 0;
    }
    50% {
    opacity: 1;
    transform: translateY(0%);
    text-shadow: 0px 5px 25px rgba(10, 21, 80, 0.5);
    }
    100% {
    opacity: 1;
    transform: translateY(0%);
    text-shadow: 0px 5px 25px rgba(10, 21, 80, 0.5);
    }
}