form{
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 12px;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textarea{
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 5px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::placeholder{
    color: #A5A5A5;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
}

textarea::focus{
    outline-color: #00ffff;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-radius: 5px;
}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    color: #202329;
    font-weight: 400;
    border: none;
    font-family: Arial, Helvetica, sans-serif;
}

.TodoForm-button--add{
    background-color: #00ffff;
    box-shadow: 0px 5px 25px rgb(97, 218, 250, 0.5);
    border-radius: 5px;    
    width: 80px;
    height: 30px;
    font-size: 15px;
}

.TodoForm-button--cancel{
    background: transparent;
    border-radius: 5px;    
    width: 80px;
    height: 30px;
    font-size: 15px;
}

