.TodoSearch{
    background: #F9FBFC;
    border-radius: 10px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: 260px;
    font-size: 24px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0pc 5pc 50pc rgba(32, 35, 41, 0.25);
}
.TodoSearch::placeholder{
    color: #A5A5A5;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
}
.TodoSearch:focus{
    outline-color: #00FFFF;
}